.App {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  height: 100vh;
  background-color: #E0b0FF;
  font-size: 25px;
  font-weight: 800;
  justify-content: center;
  align-items: center;
}

.color_picker{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 70vh;
  background-color: #FFB6C1;
  border-radius: 10px;
  box-shadow:2px 2px 5px 5px rgba(0, 0,0,0.2);
  gap: 20px;
}

.select_button{
  border: none;
  border-radius: 5px;
  padding: 15px;
  color: #fff;
  font-weight: 600;
}

